/* stylelint-disable block-closing-brace-empty-line-before, at-rule-empty-line-before */

/* stylelint-disable at-rule-empty-line-before */

/* stylelint-disable at-rule-empty-line-before */

.try-page .description {
  margin: 0 auto;
  max-width: 25rem;
  text-align: center;
}

.try-page .status {
  background: #e4e5d6;
  border-radius: 1px;
  margin-bottom: 3rem;
  padding: 1rem 1rem 1rem 2.5rem;
  position: relative;
}

.try-page .status > .icon {
  fill: #929d1b;
  left: .75rem;
  position: absolute;
  top: 1.375rem;
}

.try-page .status.-type\:error {
  background: #edd9d9;
}

.try-page .status.-type\:error > .icon {
  fill: #de3b3b;
}

.try-page .links {
  display: grid;
  grid-auto-flow: row;
  grid-gap: 2rem 5rem;
  grid-template-columns: repeat(3, auto);
  justify-content: center;
}

.try-page .links a,
.try-page .links button {
  display: block;
  margin-bottom: 1rem;
  width: 100%;
}

.try-page .links button {
  border: none;
  cursor: pointer;
}

.try-page .links button[disabled] {
  background: #777;
  cursor: progress;
}

@media screen and (max-width: 44rem) {
  .try-page .links {
    grid-template-columns: repeat(2, auto);
    grid-gap: 2rem 2rem;
  }

  .try-page .links > .demo {
    grid-column: 1 / span 2;
    order: -1;
  }
}

@media screen and (max-width: 25rem) {
  .try-page .links {
    grid-template-columns: auto;
  }

  .try-page .links > .demo {
    grid-column: initial;
  }

  .try-page .links a,
  .try-page .links button {
    width: auto;
  }
}

.try-page .starterkit {
  border: 1px solid #000;
  line-height: 0;
  margin: 0 auto 1.5rem;
  max-width: 30rem;
}




